import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { Helmet } from 'react-helmet';
import loadable from '@loadable/component';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import SectionMenu from '../components/sections/menu';
import SectionMainvisual from '../components/sections/mainvisual';
import '../index.css';

const SectionAbout = loadable(() => import('../components/sections/about'));
const SectionFacility = loadable(() =>
  import('../components/sections/facility')
);
const SectionPricing = loadable(() => import('../components/sections/pricing'));
const SectionBooking = loadable(() => import('../components/sections/booking'));
const SectionTrainer = loadable(() => import('../components/sections/trainer'));
const SectionAccess = loadable(() => import('../components/sections/access'));
const SectionContact = loadable(() => import('../components/sections/contact'));
const SectionBackgroundAddictgym = loadable(() =>
  import('../components/sections/background-addictgym')
);

const theme = createTheme({
  palette: {
    primary: {
      main: '#b28223'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32,
      letterSpacing: 1.5
    },
    h5: {
      fontSize: 28
    }
  }
});

const IndexPage = () => {
  const {
    site: {
      siteMetadata: { siteUrl, title, description }
    }
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          siteUrl
          title
          description
        }
      }
    }
  `);

  const [openingAnimation, setOpeningAnimation] = React.useState(0);
  const [isShow, setIsShow] = React.useState(false);

  React.useEffect(() => {
    // @TODO Opening animation
    const { searchParams } = new URL(window.location.href);
    const openingAnimation = Number(searchParams.get('openingAnimation'));

    if (!!openingAnimation) {
      setTimeout(() => {
        setIsShow(true);
      }, 1000);
    } else {
      setIsShow(true);
    }

    setOpeningAnimation(openingAnimation);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <Helmet>
        <title>{title}</title>
        <meta name='description' content={description} />
        <meta property='og:title' content={title} />
        <meta property='og:site_name' content={title} />
        <meta property='og:type' content='website' />
        <meta property='og:url' content={`${siteUrl}/`} />
        <meta property='og:description' content={description} />
        <meta property='og:image' content={`${siteUrl}/ogp.png`} />
        <meta property='og:image:secure_url' content={`${siteUrl}/ogp.png`} />
        <meta property='og:image:type' content='image/png' />
        <meta property='og:image:width' content='1200' />
        <meta property='og:image:height' content='630' />
        <meta property='og:locale' content='ja_JP' />
        <style>
          {`
        html {
          background-color: #2e2e2e;
        }
        `}
        </style>
      </Helmet>

      {openingAnimation === 1 && (
        <>
          <div
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              width: '100%',
              height: '50vh',
              boxSizing: 'border-box',
              borderBottom: '1px solid #b28223',
              backgroundColor: '#000',
              transformOrigin: 'top',
              transition: 'transform 500ms ease-out',
              transform: `scaleY(${isShow ? 0 : 1})`,
              zIndex: 2000
            }}
          />
          <div
            style={{
              position: 'fixed',
              bottom: 0,
              left: 0,
              width: '100%',
              height: '50vh',
              boxSizing: 'border-box',
              borderTop: '1px solid #b28223',
              backgroundColor: '#000',
              transformOrigin: 'bottom',
              transition: 'transform 500ms ease-out',
              transform: `scaleY(${isShow ? 0 : 1})`,
              zIndex: 2000
            }}
          />
        </>
      )}

      {isShow && (
        <>
          <SectionMenu />
          <SectionMainvisual />
          <SectionAbout />
          <SectionBackgroundAddictgym />
          <SectionFacility />
          <SectionPricing />
          <SectionBooking />
          <SectionTrainer />
          <SectionAccess />
          <SectionContact />
        </>
      )}
    </ThemeProvider>
  );
};

export default IndexPage;
